rem;right:-1.25rem;width:3rem;height:3rem;border-radius:50%;padding:8px;background-color:white;border:2px solid #f78d94;box-shadow:2px 2px 8px #f78d94}.trabalhos .grid-wrapper .grid-div .logo-wrapper .logo{position:relative;padding:8px;width:100%;height:100%}.trabalhos .grid-wrapper .grid-div .ss{height:50%;max-width:100%;border-radius:8px;border:2px solid #f78d94}.trabalhos .grid-wrapper .grid-div:hover{cursor:pointer;box-shadow:2px 2px 16px #f78d94}.trabalhos .grid-wrapper .grid-div:hover .logo-wrapper{box-shadow:2px 2px 16px #f78d94}@media screen and (min-width: 768px){.trabalhos .grid-wrapper{grid-template-columns:repeat(2, 1fr)}}@media screen and (min-width: 1080px){.trabalhos .grid-wrapper{grid-template-columns:repeat(3, 1fr)}}@media screen and (min-width: 1200px){.trabalhos .grid-wrapper{grid-template-columns:repeat(4, 1fr)}}.Circle{width:1.75em;height:1.75em;border:1px solid white;border-radius:50%}@media screen and (min-width: 1080px){.Circle{width:1.25em;height:1.25em}}.pattern{position:absolute;z-index:1;width:70vmin;height:70vmin}.pattern svg{top:50%;height:100%;width:100%}.pattern svg #first{fill:none;stroke-width:1}.pattern svg #second{fill:none;stroke-width:1}.pattern svg #third{fill:none;stroke-width:1;stroke:#50c5e2}.PrimaryDark{background-color:#021727;color:#fffbfe}.PrimaryDark .info{background-color:#021727}.PrimaryDark #first{stroke:#fbceb1}.PrimaryDark #second{stroke:#124e78}.PrimaryLight{background-color:#fbceb1;color:#124e78}.PrimaryLight .info{background-color:#fbceb1}.PrimaryLight #first{stroke:#021727}.PrimaryLight #second{stroke:#fffbfe}.SecondaryLight{background-color:#fffbfe;color:#021727}.SecondaryLight .info{background-color:#fffbfe}.SecondaryLight #first{stroke:#124e78}.SecondaryLight #second{stroke:#fbceb1}.SecondaryDark{background-color:#124e78;color:#fffbfe}.SecondaryDark .info{background-color:#124e78}.SecondaryDark #first{stroke:#fbceb1}.SecondaryDark #second{stroke:#021727}

